.home-container{

  min-height: 400px;
  display: flex;
  flex-direction: row;
  padding: 0 32px;

  > .left{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    

    h2{
      span{
        color: #5191D4;
        font-weight: 800;
      }
      width: 30ch;
      justify-self: center;
    }
  }

  > .right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 32px 32px 32px 0;
  }
}