.header-container{
  padding: 24px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  box-shadow: 0 1px 2px rgba(0,0,0,0.4);
  > h1{
    font-family: "Poppins",sans-serif;
  }

  .link-contato{
    display: flex;
    align-items: center;
    text-decoration: none;
    border: 1px solid black;
    border-radius: 8px;
    height: 40px;
    cursor: pointer;
    overflow: hidden;
    background: #fff;

    div{
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 12px;
      height: 100%;
      background: black;
    }

    span{
      color: black;
      font-size: 16px;
      align-self: center;
      flex: 1;
      padding: 16px 12px;
    }
  }
}